import React from 'react'
import AniLink from "gatsby-plugin-transition-link/AniLink"
import { AspectRatio,Box } from '@chakra-ui/react';
import { withPrefix } from 'gatsby';

import Container from '../../components/Container'

import Layout from '../Layout';
import { Media } from '../../components/ThemeProvider/theme'

const IndexPage = () => {
  return (
    <Layout bg="custom.lightBlueBg">
      <Media lessThan="tablet">
        <AspectRatio ratio={834 / 675}>
          <Box
            as="iframe"
            border="none"
            overflow="hidden"
            src={withPrefix('/mobile')}
            title="mobile banner"
          />
        </AspectRatio>
      </Media>
      <Media greaterThanOrEqual="tablet">
        <AspectRatio ratio={2560 / 1140}>
          <Box
            as="iframe"
            border="none"
            overflow="hidden"
            src={withPrefix('/desktop')}
            title="desktop banner"
          />
        </AspectRatio>
      </Media>
      <Container textAlign="center">
        <AniLink swipe to="q/1" direction="left">Start!</AniLink>
      </Container>
    </Layout>
  )
}

export default IndexPage
